/* 页面全局 */
html,
body {
  margin: 0;
  padding: 0;
}
body {
  background-color: #f6f6ef;
}
a {
  text-decoration: none;
  color: #000;
}
a:hover {
  color: #e00;
}
#app {
  width: 80%;
  position: relative;
  margin: 0 auto;
}
@media screen and (max-width: 700px) {
  #app {
    width: 100%;
  }
}
/* 页面头部 */
.header h1 {
  display: inline-block;
  margin: 0;
  padding: 0;
}
.header img {
  display: inline-block;
  width: 28px;
  height: 28px;
}
.nav {
  min-height: 20px;
  position: relative;
}
.nav ul,
.nav li {
  list-style: none;
}
.nav li {
  display: inline-block;
  margin: 0 2px;
  background: #828282;
}
.nav li:hover {
  background: #ccc;
}
.nav a {
  padding: 5px 10px;
  display: block;
}
.nav a.active {
  background-color: #333;
}
.nav.loading:before {
  content: "导航Loading...";
  position: absolute;
  top: 16px;
  left: 20px;
}
/* 页面尾部 */
.footer {
  text-align: center;
  height: 220px;
  line-height: 220px;
  border-top: 2px solid #ccc;
  background-color: #111;
  color: #fff;
}


